.education {
  margin-bottom: 160px;
}
@media only screen and (max-width: 1199px) {
  .education {
    margin-bottom: 60px;
  }
}
.education__caption {
  margin-bottom: 96px;
  font-size: 64px;
  font-weight: 500;
  line-height: 70.4px;
  letter-spacing: -0.02em;
}
@media only screen and (max-width: 1199px) {
  .education__caption {
    margin-bottom: 30px;
    font-size: 44px;
    font-weight: 500;
    line-height: 50.4px;
    letter-spacing: -0.02em;
  }
}
.education__timeline {
  width: 100%;
}
@media only screen and (max-width: 1200px) {
  .education__timeline {
    margin-left: -80px;
    width: calc(100% + 160px);
  }
}
@media only screen and (max-width: 1000px) {
  .education__timeline {
    display: none;
  }
}
.education__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: none;
  flex-direction: column;
  gap: 24px;
}
@media only screen and (max-width: 1000px) {
  .education__list {
    display: flex;
  }
}
.education__item {
  display: flex;
  align-items: center;
  gap: 24px;
  width: 100%;
}
.education__item:not(:last-child) {
  padding-bottom: 24px;
  border-bottom: 1px dashed rgba(26, 26, 26, 0.6392156863);
}
.education__item-content {
  list-style: disc;
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  line-height: 23.4px;
  letter-spacing: 0.01em;
}
.education__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}
.education__icon img {
  height: 40px;
  width: 40px;
}
.education__year {
  padding-left: 40px;
  font-size: 38px;
  font-weight: 500;
  line-height: 47.6px;
  letter-spacing: 0.01em;
}