.header {
  position: absolute;
  width: 100%;
  z-index: 10;
}
@media only screen and (max-width: 1300px) {
  .header__button {
    display: none;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  padding: 32px 0;
}
@media only screen and (max-width: 900px) {
  .header__wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    padding: 20px 0;
  }
}
.header__logo {
  display: flex;
  gap: 24px;
  align-items: center;
  margin-right: 32px;
}
.header__logo-caption {
  font-size: 14px;
  line-height: 20px;
  color: #1A1A1A;
  opacity: 0.65;
}
.header__logo-name {
  font-weight: 800;
  font-size: 42px;
  line-height: 51px;
  color: #1A1A1A;
}
@media only screen and (max-width: 900px) {
  .header__logo {
    margin-right: 0;
  }
  .header__logo-caption {
    font-size: 10px;
    line-height: 16px;
  }
  .header__logo-name {
    font-size: 32px;
    line-height: 41px;
  }
}
.header__address {
  display: inline-block;
  font-style: normal;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0.01em;
  text-align: left;
  color: #1A1A1A;
  opacity: 0.72;
}
@media only screen and (max-width: 1272px) {
  .header__address {
    display: none;
  }
}
.header__contacts {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: 24px;
}
.header__contacts a {
  color: #ffffff;
  transition: color 0.3s;
  opacity: 0.96;
  letter-spacing: 0.01em;
}
@media (hover: hover) {
  .header__contacts a:hover {
    color: #e2e2e2;
  }
}
.header__contacts-tel {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
  text-align: center;
}
.header__contacts-mail {
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
}
@media only screen and (max-width: 1272px) {
  .header__contacts {
    margin-right: 0;
  }
}
@media only screen and (max-width: 900px) {
  .header__contacts {
    margin-left: 0;
    display: none;
  }
}
.header .social {
  display: flex;
  gap: 8px;
  margin-left: 24px;
}
.header .social__icon {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 64px;
  height: 64px;
  line-height: 0;
  margin-right: 10px;
  background-color: #ffffff;
  border-radius: 50%;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
}
@media (hover: hover) {
  .header .social__icon:hover {
    background-color: #f3f3f3;
  }
}
@media only screen and (max-width: 900px) {
  .header .social {
    margin-left: 0;
    display: none;
  }
}