@charset "UTF-8";
.slider {
  margin-bottom: 160px;
}
.slider--news {
  margin-bottom: 64px;
}
@media only screen and (max-width: 1199px) {
  .slider {
    margin-bottom: 60px;
  }
  .slider--news {
    margin-bottom: 34px;
  }
}
.slider__more, .slider__more-news {
  margin-left: 24px;
}
@media only screen and (max-width: 900px) {
  .slider__more, .slider__more-news {
    margin-left: 0;
  }
}
.slider__header {
  display: flex;
  margin-bottom: 42px;
  align-items: center;
}
@media only screen and (max-width: 900px) {
  .slider__header--reviews {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin-bottom: 20px;
  }
}
.slider__header-wrapper {
  display: flex;
  flex-direction: column;
}
.slider__header-wrapper span {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 31.2px;
  letter-spacing: -0.01em;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: rgba(49, 49, 49, 0.6392156863);
}
.slider__controls {
  display: flex;
  gap: 8px;
  margin-left: auto;
}
.slider__controls-button {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0.1607843137);
  background-color: #ffffff;
  transition: background-color 0.3s;
}
@media (hover: hover) {
  .slider__controls-button:hover {
    background-color: #e6e6e6;
  }
}
@media only screen and (max-width: 900px) {
  .slider__controls {
    display: none;
  }
}
@media only screen and (max-width: 900px) {
  .slider__controls-news {
    margin: 0;
  }
}
.slider__icon {
  position: absolute;
  z-index: 11;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.slider .slider-certificats {
  width: 100%;
}
.slider .slider-certificats .slick-slide {
  overflow: hidden;
  height: 316px;
  min-width: 408px;
  border-radius: 8px;
  position: relative;
}
@media only screen and (max-width: 900px) {
  .slider .slider-certificats .slick-slide {
    min-width: auto;
    height: auto;
  }
}
.slider .slider-certificats .slick-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top center;
}
.slider .slider-certificats .slick-track {
  display: flex;
  gap: 32px;
}
@media only screen and (max-width: 900px) {
  .slider .slider-certificats .slick-track {
    gap: 12px;
  }
}
.slider .slider-blog {
  width: 100%;
}
.slider .slider-blog .slick-slide {
  overflow: hidden;
  height: 530px;
  min-width: 408px;
  border-radius: 8px;
  position: relative;
}
.slider .slider-blog .slick-slide img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.slider .slider-blog .slick-slide::before {
  content: "";
  inset: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: absolute;
  z-index: 1;
  transition: background-color 0.3s;
}
@media only screen and (max-width: 796px) {
  .slider .slider-blog .slick-slide {
    min-width: auto;
    height: 316px;
  }
}
@media (hover: hover) {
  .slider .slider-blog .slick-slide:hover::before {
    background-color: transparent;
  }
}
.slider .slider-blog .slick-track {
  display: flex;
  gap: 16px;
}
@media only screen and (max-width: 900px) {
  .slider .slider-blog .slick-track {
    gap: 8px;
  }
}
.slider .slider-reviews {
  width: 100%;
}
.slider .slider-reviews .slick-slide {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 427px;
  min-width: 408px;
  border-radius: 8px;
  position: relative;
  cursor: pointer;
  border: 2px solid rgba(26, 26, 26, 0.1607843137);
}
@media only screen and (max-width: 796px) {
  .slider .slider-reviews .slick-slide {
    min-width: auto;
  }
}
.slider .slider-reviews .slick-track {
  display: flex;
  gap: 16px;
}
@media only screen and (max-width: 900px) {
  .slider .slider-reviews .slick-track {
    gap: 8px;
  }
}
.slider .slider-news {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 32px;
}
@media only screen and (max-width: 1500px) {
  .slider .slider-news {
    gap: 16px;
  }
}
@media only screen and (max-width: 1200px) {
  .slider .slider-news {
    gap: 8px;
  }
  .slider .slider-news .news__slide {
    width: 100%;
  }
}

.certificats__caption {
  font-size: 64px;
  font-weight: 500;
  line-height: 70.4px;
  letter-spacing: -0.02em;
}
@media only screen and (max-width: 1400px) {
  .certificats__caption {
    font-size: 44px;
    line-height: 50.4px;
  }
}

.blog__caption {
  font-size: 64px;
  font-weight: 500;
  line-height: 70.4px;
  letter-spacing: -0.02em;
}
@media only screen and (max-width: 1400px) {
  .blog__caption {
    font-size: 44px;
    line-height: 50.4px;
  }
}

.news__caption {
  font-size: 64px;
  font-weight: 500;
  line-height: 70.4px;
  letter-spacing: -0.02em;
}
@media only screen and (max-width: 1400px) {
  .news__caption {
    font-size: 44px;
    line-height: 50.4px;
  }
}
.news__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: absolute;
  padding-left: 5px;
  z-index: 1;
  bottom: 0;
  right: 0;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-color: #ffffff;
  transition: background-color 0.3s;
}
@media only screen and (max-width: 1300px) {
  .news__icon {
    width: 32px;
    height: 32px;
  }
}
.news__link {
  position: relative;
  display: flex;
  height: 333px;
  padding: 20px;
  border-radius: 16px;
  overflow: hidden;
}
@media only screen and (max-width: 1500px) {
  .news__link {
    height: 233px;
  }
}
@media only screen and (max-width: 1200px) {
  .news__link {
    height: 150px;
  }
}
.news__link::before {
  content: "";
  inset: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: absolute;
  z-index: 1;
  transition: background-color 0.3s;
}
.news__link-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  z-index: 1;
}
.news__link-content .news__link-heading {
  width: 50%;
}
@media only screen and (max-width: 1200px) {
  .news__link-content .news__link-heading {
    width: 100%;
  }
}
.news__link-content--column {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.news__link-content--column .news__link-heading {
  width: 100%;
}
.news__link-heading {
  display: inline-block;
  font-family: Manrope;
  font-size: 20px;
  font-weight: 600;
  line-height: 26px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #ffffff;
  margin-bottom: 33px;
}
@media only screen and (max-width: 1500px) {
  .news__link-heading {
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
  }
}
.news__link-data {
  font-family: Manrope;
  font-size: 16px;
  font-weight: 500;
  line-height: 20.8px;
  letter-spacing: 0.01em;
  text-align: left;
  color: rgba(255, 255, 255, 0.6392156863);
}
@media only screen and (max-width: 1500px) {
  .news__link-data {
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
  }
}
@media only screen and (max-width: 1200px) {
  .news__link-data {
    display: none;
  }
}
.news__link img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (hover: hover) {
  .news__link:hover::before {
    background-color: transparent;
  }
  .news__link:hover .news__icon {
    background-color: #0B766B;
  }
}
.news .grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  padding: 0 10px;
  gap: 32px;
}
@media only screen and (max-width: 1500px) {
  .news .grid {
    gap: 16px;
  }
}
@media only screen and (max-width: 1200px) {
  .news .grid {
    gap: 8px;
  }
}
@media only screen and (max-width: 1000px) {
  .news .grid {
    grid-template-columns: repeat(2, 1fr); /*  2  */
  }
}
@media only screen and (max-width: 900px) {
  .news .grid {
    display: flex;
    flex-direction: column;
    padding: 0 4px;
  }
}
.news__link:nth-child(1) {
  height: 100%;
  grid-row: span 2/span 2;
  background-color: red;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(1) {
    height: 150px;
    grid-column-start: auto;
    grid-row-start: auto;
  }
}
@media only screen and (max-width: 796px) {
  .news__link:nth-child(1) {
    grid-row: auto;
    align-self: stretch; /*       */
  }
}
.news__link:nth-child(2) {
  height: 100%;
  grid-row: span 2/span 2;
  background-color: green;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(2) {
    height: 150px;
    grid-column-start: auto;
    grid-row-start: auto;
  }
}
.news__link:nth-child(3) {
  grid-column: span 2/span 2;
  background-color: #000000;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(3) {
    grid-column-start: auto;
    grid-row-start: auto;
  }
}
.news__link:nth-child(4) {
  grid-column: span 2/span 2;
  grid-column-start: 3;
  grid-row-start: 2;
  background-color: #0B766B;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(4) {
    grid-column: span 2;
    grid-column-start: auto;
    grid-row-start: auto;
  }
}
.news__link:nth-child(5) {
  grid-row-start: 3;
  background-color: cadetblue;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(5) {
    grid-column: span 2;
    grid-column-start: auto;
    grid-row-start: auto;
  }
}
.news__link:nth-child(6) {
  grid-column: span 2;
  grid-row-start: 3;
  background-color: saddlebrown;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(6) {
    grid-column: span 2;
    grid-column-start: auto;
    grid-row-start: auto;
  }
}
.news__link:nth-child(7) {
  grid-row-start: 3;
  background-color: forestgreen;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(7) {
    grid-column: span 2;
    grid-column-start: auto;
    grid-row-start: auto;
  }
}
.news__link:nth-child(8) {
  grid-row-start: 3;
  background-color: lavenderblush;
}
@media only screen and (max-width: 1000px) {
  .news__link:nth-child(8) {
    grid-column: span 2;
    grid-column-start: auto;
    grid-row-start: auto;
  }
}

.reviews__caption {
  font-size: 64px;
  font-weight: 500;
  line-height: 70.4px;
  letter-spacing: -0.02em;
}
@media only screen and (max-width: 1400px) {
  .reviews__caption {
    font-size: 44px;
    line-height: 50.4px;
  }
}
.reviews .review {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 48px;
}
@media only screen and (max-width: 900px) {
  .reviews .review {
    padding: 20px;
  }
}
.reviews .review__rating {
  margin-bottom: 16px;
}
.reviews .review__rating-value, .reviews .review__rating-line {
  display: flex;
}
.reviews .review__rating-line {
  position: relative;
}
.reviews .review__rating-value {
  position: absolute;
  left: 0;
}
.reviews .review__name {
  font-size: 20px;
  font-weight: 600;
  line-height: 26px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #313131;
  margin-bottom: 32px;
}
.reviews .review__text {
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.8px;
  letter-spacing: 0.01em;
  color: rgba(26, 26, 26, 0.7215686275);
  margin-bottom: 32px;
}
.reviews .review__text--cutted {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5; /*     */
  overflow: hidden;
  text-overflow: ellipsis; /*     */
}
.reviews .review__controls {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: auto;
}
.reviews .review__data {
  font-size: 14px;
  font-weight: 500;
  line-height: 18.2px;
  letter-spacing: 0.01em;
  text-align: left;
  color: rgba(0, 0, 0, 0.2392156863);
}
@media only screen and (max-width: 900px) {
  .reviews .review__data {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
  }
}
.reviews .review__more {
  position: relative;
  z-index: 10;
  font-family: Manrope;
  font-size: 16px;
  font-weight: 800;
  line-height: 20.8px;
  letter-spacing: 0.01em;
  text-align: left;
  text-transform: uppercase;
  color: #0B766B;
  transition: opacity 0.3s;
}
@media only screen and (max-width: 900px) {
  .reviews .review__more {
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
  }
}
@media (hover: hover) {
  .reviews .review__more:hover {
    opacity: 0.7;
  }
}