.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.form__input {
  width: 100%;
}
.form__button {
  width: 100%;
}
.form__checkbox {
  display: none;
}
.form__button {
  margin-top: 16px;
}
.form textarea {
  height: 94px;
  resize: none;
}
.form textarea::-webkit-scrollbar, .form textarea::-webkit-scrollbar-thumb {
  display: none;
}
.form input, .form textarea {
  border: none;
  background-color: #ffffff;
  border-radius: 64px;
  padding: 25px 42px;
  font-family: Inter;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  color: #214250;
}
.form__label {
  display: block;
  cursor: pointer;
}
.form__label--checkbox {
  position: relative;
  font-size: 12px;
  font-weight: 400;
  line-height: 16.8px;
  letter-spacing: 0.01em;
  text-align: left;
  color: rgba(255, 255, 255, 0.6392156863);
  padding-left: 36px;
}
.form__label::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  display: inline-block;
  width: 18px;
  height: 18px;
  background-color: #ffffff;
  border-radius: 3px;
  vertical-align: middle;
}
.form__checkbox:checked + .form__label::before {
  background-image: url("../img/check.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

@media only screen and (max-width: 769px) {
  .form__label {
    text-align: center;
  }
}