.nav {
  width: 100%;
  margin-bottom: 90px;
  transition: opacity 1s;
}
.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
.nav__item {
  flex-shrink: 0;
  border-radius: 64px;
  overflow: hidden;
}
.nav__item.is-current {
  background-color: #F1F1F1;
}
.nav__item-link {
  display: flex;
  color: rgba(26, 26, 26, 0.8588235294);
  text-transform: uppercase;
  padding: 20px 32px;
  font-size: 18px;
  font-weight: 500;
  line-height: 25px;
  letter-spacing: 0.02em;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
}
.nav__item-link--light {
  color: #F1F1F1;
}
@media (hover: hover) {
  .nav__item-link:hover {
    opacity: 0.7;
  }
}
@media only screen and (max-width: 1690px) {
  .nav__item a {
    padding: 8px 14px;
  }
}
@media only screen and (max-width: 900px) {
  .nav__item a {
    font-size: 14px;
    line-height: 20px;
  }
}
@media only screen and (max-width: 1250px) {
  .nav {
    position: absolute;
    z-index: 10;
    top: 128px;
    left: 0;
    background-color: rgba(207, 207, 207, 0.5);
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
    padding: 0 20px;
  }
}
@media only screen and (max-width: 900px) {
  .nav {
    overflow-x: scroll;
  }
}

.nav-dynamic {
  width: 100%;
  padding: 4px 0;
  position: fixed;
  left: 0;
  z-index: 11;
  transition: transform 0.5s;
  pointer-events: none;
  transform: translateY(-100%);
  background-color: rgba(51, 175, 115, 0.9019607843);
  backdrop-filter: blur(5px);
  border-bottom: 2px solid #2c9864;
}
@media only screen and (max-width: 900px) {
  .nav-dynamic {
    overflow-x: scroll;
  }
}

body.is-scroll .nav {
  opacity: 0;
}
body.is-scroll .nav-dynamic {
  transform: none;
  opacity: 1;
  pointer-events: auto;
}