.consultation {
  margin-bottom: 160px;
}
@media only screen and (max-width: 1199px) {
  .consultation {
    margin-top: 160px;
    margin-bottom: 60px;
  }
}
@media only screen and (max-width: 1000px) {
  .consultation {
    margin-top: 0px;
  }
}
.consultation__content {
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 1000px) {
  .consultation__content {
    flex-direction: column;
    gap: 36px;
  }
}
.consultation__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 65%;
  gap: 80px;
  padding-right: 62px;
}
@media only screen and (max-width: 1400px) {
  .consultation__wrapper {
    padding-right: 20px;
    gap: 44px;
  }
}
@media only screen and (max-width: 1000px) {
  .consultation__wrapper {
    padding-right: 0;
    width: 100%;
    gap: 34px;
  }
}
.consultation__left {
  position: absolute;
  top: calc(50% - 26px);
  right: 0;
  display: flex;
  width: 52px;
  height: 52px;
  border: 2px solid rgba(0, 0, 0, 0.1607843137);
  background-color: #ffffff;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
@media only screen and (max-width: 1400px) {
  .consultation__left {
    display: none;
  }
}
.consultation__warning {
  display: flex;
  gap: 24px;
  align-items: center;
}
.consultation__warning-text {
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.01em;
  color: rgba(26, 26, 26, 0.4196078431);
}
.consultation__heading {
  font-size: 64px;
  font-weight: 500;
  line-height: 70.4px;
  letter-spacing: -0.02em;
}
@media only screen and (max-width: 1399px) {
  .consultation__heading {
    font-size: 44px;
    line-height: 50.4px;
  }
}
@media only screen and (max-width: 1000px) {
  .consultation__heading {
    text-align: center;
  }
}
.consultation__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 32px;
}
.consultation__item {
  width: 48%;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 800px) {
  .consultation__item {
    width: 100%;
  }
}
.consultation__icon {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  margin-right: 32px;
}
.consultation__icon-lightning {
  position: absolute;
  width: 32px;
  height: 32px;
  top: calc(50% - 16px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  right: -16px;
}
.consultation__form {
  width: 548px;
  background-color: #0B766B;
  padding: 90px 100px;
  border-radius: 24px;
  margin-left: 30px;
}
@media only screen and (max-width: 1400px) {
  .consultation__form {
    margin-right: -40px;
    padding: 90px 40px 30px 20px;
  }
}
@media only screen and (max-width: 1199px) {
  .consultation__form {
    width: 40%;
  }
}
@media only screen and (max-width: 1000px) {
  .consultation__form {
    width: 100%;
    margin: 0;
    margin-top: 90px;
  }
}
.consultation__form-header {
  position: relative;
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-top: -180px;
  margin-bottom: 36px;
  padding-bottom: 16px;
}
.consultation__form-header::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(51, 175, 115, 0.6392156863);
}
.consultation__form-name {
  font-size: 16px;
  font-weight: 500;
  line-height: 19.2px;
  letter-spacing: -0.02em;
  text-align: center;
  color: #48D18F;
}
.consultation__form-caption {
  display: inline-block;
  font-size: 24px;
  font-weight: 700;
  line-height: 28.8px;
  letter-spacing: 0.01em;
  text-align: center;
  color: #F2FFF9;
  margin-bottom: 36px;
  width: 100%;
}
.consultation__form-header-img {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 174px;
  height: 174px;
  border-radius: 50%;
  border: 1px solid #CECECE;
  border-bottom: none;
  background-color: #ffffff;
  overflow: hidden;
}
.consultation__form-header-inner-img {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  background-color: #ffffff;
  overflow: hidden;
}