.contacts__map {
  position: relative;
}
.contacts__map--wrapper {
  height: 751px;
  border-radius: 16px;
  overflow: hidden;
}
@media only screen and (max-width: 1000px) {
  .contacts__map {
    display: flex;
    flex-direction: column-reverse;
    gap: 30px;
    margin-top: 80px;
    overflow: hidden;
  }
  .contacts__map--wrapper {
    height: 300px;
  }
}
.contacts__info {
  position: absolute;
  top: 60px;
  left: 60px;
  padding: 64px 90px;
  border-radius: 16px;
  background-color: #FFFFFF;
  border: 2px solid rgba(0, 0, 0, 0.1607843137);
}
@media only screen and (max-width: 1600px) {
  .contacts__info {
    top: 20px;
    left: 20px;
    padding: 20px;
  }
}
@media only screen and (max-width: 1000px) {
  .contacts__info {
    position: relative;
    top: auto;
    left: auto;
    padding: 0;
    border: none;
  }
}
.contacts__list {
  padding: 0;
  margin: 0;
  display: flex;
  width: 339px;
  gap: 32px;
  flex-direction: column;
  list-style: none;
}
@media only screen and (max-width: 1000px) {
  .contacts__list {
    width: 100%;
  }
}
.contacts__item {
  display: flex;
  align-items: center;
  gap: 24px;
}
.contacts__item-data {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.01em;
}
.contacts__item-name {
  font-weight: 600;
  color: rgba(0, 0, 0, 0.8588235294);
}
.contacts__item-value {
  color: rgba(0, 0, 0, 0.6392156863);
}
.contacts__button {
  width: 100%;
  margin-top: 42px;
}
.contacts__heading {
  font-size: 24px;
  font-weight: 700;
  line-height: 28.8px;
  letter-spacing: 0.01em;
  text-align: center;
  color: #1A1A1A;
  margin-bottom: 42px;
}
.contacts__icon {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-color: rgba(26, 26, 26, 0.0392156863);
}